.subscription {
  background-image: url(/images/subscription-img.jpg);
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
  box-sizing: border-box;
  padding: 88px 0;

  @include onTablet {
    padding: 82px 0;
  }

  @include onSmallDesktop {
    padding: 163px 0;
  }

  &__title {
    margin: 0 0 20px;
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    text-align: center;
    color: $c-white;

    @include onTablet {
      margin-bottom: 16px;
      font-size: 48px;
    }
  }

  &__text {
    margin: 0 0 50px;
    font-family: "IBM Plex Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    text-align: center;
    color: $c-white;

    @include onTablet {
      margin-bottom: 64px;
      line-height: 100%;
    }

    @include onSmallDesktop {
      margin-bottom: 72px;
    }
  }

  &__form {
    display: flex;
    justify-content: center;
    margin: 0 auto;
  }

  &__button {
    width: 50px;
    border: none;
    background: $c-orange;
    background-image: url(/images/subscription/Vector.svg);
    background-position: center;
    background-repeat: no-repeat;
    transition: all $transitionTime;

    &:hover {
      background-color: $c-green;
    }

    &:active {
      background-color: $c-green;
      width: 65px;
      margin-left: 0;

      @include onTablet {
        width: 86px;
      }

      @include onSmallDesktop {
        width: 100px;
      }
    }

    @include onTablet {
      width: 70px;
      height: 70px;
      margin-left: 16px;
    }

    @include onSmallDesktop {
      margin-left: 32px;
    }
  }
}
