.slider {
  overflow: hidden;

  &__box {
    display: flex;
    margin-bottom: 20px;
    justify-content: start;

    @include onTablet {
      margin-bottom: 32px;
    }
    
    @include onDesktop {
      display: grid;
      grid-template-columns: repeat(12,1fr);
      gap: 30px;
      width: 100%;
      margin-bottom: 0;
    }
  }

  &__image {
    box-sizing: border-box;
    height: 410px;
    width: 280px;
    object-fit: cover;
    margin-right: 16px;
    transition: all $transitionTime;

    &:hover {
      transform: scale(1.02);
    }

    @include onTablet {
      height: 543px;
      width: 340px;
      margin-right: 20px;
    }

    @include onSmallDesktop {
      margin-bottom: 20px;
    }

    @include onDesktop {
      margin-bottom: 0;

      &:last-child {
        margin-right: 0;
      }
  
      &--1 {
        grid-column: 1/5;
        width: 100%;
        height: 600px;
      }
  
      &--2 {
        grid-column: 5/13;
        width: 100%;
        height: 600px;
      }
  
      &--3 {
        grid-column: 1/9;
        width: 100%;
        height: 360px;
      }
      
      &--4 {
        grid-column: 9/13;
        width: 100%;
        height: 360px;
      }
    }
  }

  &__nav {
    display: flex;
    gap: 22px;
    justify-content: center;
  }

  &__nav-item {
    height: 9px;
    width: 9px;
    background: $c-gray;
    border-radius: 50%;

    @include onDesktop {
      display: none;
    }

    &--active {
      background: $c-green;
    }
  }
}
