.events-box {
  margin-bottom: 60px;
  display: flex;
  flex-direction: column;
  cursor: pointer;

  @include onTablet {
    margin-bottom: 64px;
  }

  @include onSmallDesktop {
    justify-content: space-between;
  }

  &--exhibition {
    @include onSmallDesktop {
      margin-bottom: 104px;
      flex-direction: row;
      align-items: center;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }

  &:nth-child(2) {
    @include onTablet {
      margin-bottom: 112px;
    }

    @include onSmallDesktop {
      margin-bottom: 104px;
    }
  }
  
  &__photo {
    margin-bottom: 16px;
    transition: all $transitionTime;
    object-fit: cover;

    &:hover {
      transform: scale(1.02);
    }

    @include onTablet {
      margin-bottom: 40px;
    }

    @include onSmallDesktop {

      width: 57%;
      margin-bottom: 0;
    }

    @include onDesktop {
      height: 520px;
      width: 670px;
      margin-right: 130px;
    }
  }

  &__text {
    @include onSmallDesktop {
      width: 370px;
    }
  }

  &__info {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;

    @include onTablet {
      width: 220px;
      margin-bottom: 30px;
    }

    &--lecture {
      margin-bottom: 0;
      padding: 20px;

      @include onTablet {
        padding: 45px 0 16px;
        width: 370px;
      }

      @include onDesktop {
        width: 330px;
      }
    }
  }

  &__type {
    font-family: "IBM Plex Sans", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: $c-grey;

    @include onTablet {
      font-size: 14px;
    }
  }

  &__date {
    padding-right: 3px;
    font-family: "IBM Plex Sans", sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    color: $c-green;

    @include onTablet {
      font-size: 14px;
      padding-right: 0;
    }
  }

  &__title {
    display: flex;
    margin: 0 0 10px;
    height: 34px;
    min-width: 280px;
    align-items: center;

    @include onTablet {
      margin-bottom: 15px;
      height: 39px;
    }
  }

  &__title-link {
    align-self: start;
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
    font-size: 28px;
    line-height: 100%;
    color: $c-black;
    text-decoration: none;
    transition: all $transitionTime;

    &:hover {
      color: $c-orange;
    }

    @include onTablet {
      font-size: 36px;
    }
  }

  &__title-square {
    margin-left: 10px;
    height: 10px;
    width: 10px;
    background-color: $c-orange;
    transition: all 150ms;

    @include onTablet {
      margin-left: 11px;
      height: 15px;
      width: 15px;
    }
  }

  &__about {
    margin: 0;
    max-width: 460px;
    font-family: "IBM Plex Sans", sans-serif;
    font-weight: 300;
    font-size: 16px;
    line-height: 150%;
    color: $c-darkGrey;
  }

  &__image {
    position: absolute;
    z-index: -1;
    left: 0;
    right: 0;
    height: 210px;
    background-size: cover;
    background-position: center;
    background-image: url(../images/events/events-3.jpg);

    @include onTablet {
      height: 390px;
      left: 34px;
      right: 34px;
    }

    @include onSmallDesktop {
      height: 550px;
      left: 0;
      right: 0;
    }
  }

  &__text-lecture {
    display: flex;
    flex-direction: column;
    height: 140px;
    max-width: 570px;
    margin: 153px auto 0;
    box-sizing: border-box;
    background: $c-white;
    text-align: center;
    box-shadow: 0 2px 0 rgba(26, 90, 76, 0.25);

    @include onTablet {
      width: 520px;
      text-align: start;
      margin-top: 306px;
      height: 204px;
      padding: 0 55px 50px;
    }

    @include onTablet768 {
      width: 570px;
    }

    @include onSmallDesktop {
      margin-top: 448px;
      padding: 0 100px 50px;
    }
  }
}
