.events {
  &__title {
    margin: 0 auto 40px;
    font-family: "Montserrat", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    text-align: center;
    color: $c-black;

    @include onTablet {
      margin-bottom: 48px;
      font-weight: 700;
      font-size: 48px;
      text-align: start;
    }

    @include onDesktop {
      margin-bottom: 58px;
    }
  }
}
