.galery {
  &__title {
    margin: 0 0 40px;
    text-align: center;
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    color: $c-black;

    @include onTablet {
      font-size: 48px;
      text-align: left;
      margin-bottom: 48px;
    }

    @include onSmallDesktop {
      margin-bottom: 56px;
    }
  }
}
