.page {
  font-family: "Montserrat", sans-serif;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    background: $c-white;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__header {
    position: relative;
  }

  &__background-image {
    z-index: -1;
    position: absolute;
    height: 300px;
    top: 0;
    left: 0;
    right: 0;
    background-size: cover;
    background-position: center;
    background-image: url(../images/header/background-image.jpg);

    @include onTablet {
      height: 384px;
      left: 51%;
      right: 0;
    }

    @include onDesktop {
      height: 655px;
      left: 43%;
    }
  }

  &__events-button {
    position: relative;
    right: 62px;
    top: 16px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateY(-100%);
    transition: $transitionTime;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-top: 100px;

    &--events {
      margin-top: 70px;
    }
    
    @include onTablet {
      margin-top: 88px;
    }

    @include onDesktop {
      margin-top: 200px;
    }
  }
}
