.form-field {
  height: 50px;
  padding-left: 24px;
  box-sizing: border-box;
  border: none;
  background-color: $c-white;
  font-size: 14px;
  line-height: 100%;
  color: $c-black;

  &::placeholder {
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    color: $c-grey;
    transition: all $transitionTime;
  }

  &:hover {
    &::placeholder {
      color: $c-black;
    }
  }

  &:focus {
    outline: none;
    
    &::placeholder {
      color: $c-black;
    }
  }

  @include onMobile {
    max-width: 374px;
    flex-grow: 1;
  }

  @include onTablet {
    width: 374px;
    height: 70px;
  }

  @include onSmallDesktop {
    width: 470px;
    padding-left: 32px;
  }
}
