.footer {
  padding-top: 50px;
  font-family: "IBM Plex Sans", sans-serif;
  color: $c-black;

  @include onSmallDesktop {
    padding-top: 48px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;

    @include onTablet {
      padding-bottom: 53px;
    }

    @include onTablet768 {
      padding-bottom: 0;
    }
  }

  &__top {
    --columns: 2;
    display: grid;
    column-gap: 20px;
    grid-template-columns: repeat(var(--columns), 1fr);

    @include onTablet {
      --columns: 6;
    }

    @include onSmallDesktop {
      --columns: 12;
      column-gap: 30px;
    }
  }

  &__icons {
    grid-column: 1 / -1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;

    @include onTablet {
      grid-column: 1 / 2;
      flex-direction: column;
      justify-content: start;
      align-items: start;
    }
  }
  
  &__logo {
    @include onTablet {
      margin-bottom: 62px;
      margin-left: 2px;
    }
  }

  &__social {
    display: flex;
    gap: 20px;

    @include onTablet {
      gap: 32px;
    }

    @include onSmallDesktop {
      gap: 22px;
    }
  }

  &__social-icon {
    height: 24px;
    width: 24px;
    background-repeat: no-repeat;
    background-position: center;
    transition: all $transitionTime;

    &--facebook {
      background-image: url(/images/footer/facebook-icon.svg);
      
      &:hover {
        background-image: url(/images/footer/facebook-icon-hover.svg);
      }
    }
  
    &--instagram {
      background-image: url(/images/footer/instagram-icon.svg);
  
      &:hover {
        background-image: url(/images/footer/instagram-icon-hover.svg);
      }
    }
  }

  &__calendar {
    grid-column: 1 / 2;
    display: flex;
    flex-direction: column;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;

    @include onTablet {
      grid-column: 2 / 4;
    }

    @include onSmallDesktop {
      grid-column: 3 / 5;
    }
  }

  &__contact-us {
    grid-column: 2;
    display: flex;
    flex-direction: column;
    padding-right: 3px;

    @include onTablet {
      grid-column: 4 / 6;
    }

    @include onSmallDesktop {
      grid-column: 5 / 7;
    }
  }

  &__title {
    margin-bottom: 20px;
    line-height: 140.62%;
  }

  &__text {
    margin: 0;

    @include onDesktop {
      width: 170px;
    }
  }

  &__button {
    grid-column: 2;
    margin-left: auto;
    width: 50px;
    height: 50px;
    box-sizing: border-box;
    border: 1px solid $c-green;
    border-radius: 50%;
    background-image: url(/images/footer/Vector.svg);
    background-position: center;
    background-repeat: no-repeat;
    transition: all $transitionTime;

    &:hover {
      transform: scale(1.1);
    }

    @include onTablet {
      display: none;
    }
  }

  &__line {
    grid-column: 1 / -1;
    height: 1px;
    margin: 20px 0 40px;
    background: hsla(167, 55%, 23%, 0.5);

    @include onTablet {
      grid-column: 2 / 6;
      margin: 64px 0;
    }

    @include onSmallDesktop {
      width: 1px;
      height: 260px;
      grid-column: 7 / 8;
      margin: 0 auto;
    }
  }

  &__nav {
    grid-column: 1 / -1;
    display: grid;
    column-gap: 20px;
    grid-template-columns: repeat(2, 1fr);

    @include onTablet {
      grid-column: 2 / 6;
    }

    @include onSmallDesktop {
      grid-column: 8 / 12;
      column-gap: 30px;
      align-self: flex-start;
    }
  }

  &__link {
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 100%;
    transition: all $transitionTime;

    &:hover {
      color: $c-orange;
      border-right: 1px solid $c-orange;
    }

    &:active {
      border-right: 3px solid $c-orange;
    }

    &--1 {
      grid-column: 1/2;
    }

    &--2 {
      grid-column: 2/3;
    }
  }

  &__copyright {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 44px;
    margin: 60px 0;
    color: $c-black;
    text-align: center;
    font-size: 12px;
    font-weight: 300;
    line-height: 17px;

    @include onTablet {
      flex-direction: row-reverse;
      margin: 100px 0 36px;
    }

    @include onSmallDesktop {
      margin: 87px 0 35px;
    }
  }
}
