.menu {
  height: 100vh;
  background: hwb(240 9% 89% / 0.5);
  font-family: "IBM Plex Sans", sans-serif;
  color: $c-white;

  &__conteiner {
    height: 400px;
    background: $c-green;

    @include onTablet {
      height: auto;
    }

    @include onTablet768 {
      height: 225px;
    }

    @include onDesktop {
      height: 233px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding-top: 27px;

    @include onTablet {
      flex-direction: row;
      padding-bottom: 53px;
      padding-top: 39px;
    }

    @include onTablet768 {
      padding-bottom: 0;
    }
  }

  &__nav {
    display: flex;
    align-items: flex-start;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);

    @include onTablet {
      min-width: 46%;
      border-right: 1px solid rgba(255, 255, 255, 0.5);
      border-bottom: none;
    }

    @include onDesktop {
      min-width: 51%;
    }
  }

  &__list {
    height: 96px;
    margin: 0 0 0 21px;
    width: 244px;
    display: flex;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;

    @include onTablet {
      margin: 0 auto;
      width: 236px;
    }

    @include onDesktop {
      height: 126px;
      width: 384px;
    }
  }

  &__link {
    margin: 0 20px 24px 0;
    width: 100px;
    padding-left: 5px;
    border-left: 3px solid transparent;
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    color: $c-white;
    text-decoration: none;
    transition: all $transitionTime;

    &:hover {
      border-left: 3px solid $c-white;
    }

    &:active {
      color: rgba(255, 255, 255, 0.76);
      border-left: 3px solid rgba(255, 255, 255, 0.76);
    }

    @include onTablet {
      margin-right: 12px;
    }
    

    @include onDesktop {
      margin: 0 23px 30px 0;
      width: 169px;
      font-size: 22px;

    }
  }

  &__calendar {
    display: flex;
    flex-direction: column;
    margin: 24px 0 27px;
    font-weight: 400;
    font-size: 14px;

    @include onTablet {
      margin: 0 auto;
      padding-left: 38px;
    }

    @include onDesktop {
      padding-left: 0;
    }
  }

  &__title {
    margin-bottom: 20px;
    line-height: 140.62%;
  }

  &__days {
    display: flex;
    flex-direction: column;
    line-height: 150%;

    @include onTablet {
      width: 370px;
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__working-days {
    margin: 0 0 16px;
    min-width: 158px;

    @include onTablet {
      margin: 0;
      padding-right: 38px;
    }

    @include onDesktop {
      width: 170px;
      padding-right: 30px;
    }
  }

  &__weekend {
    @include onTablet {
      max-width: 144px;
    }

    @include onDesktop {
      max-width: 170px;
    }
  }
}
