.header {

  @include onSmallDesktop {
    height: 655px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding: 9px 0 0;

    @include onTablet {
      padding-top: 15px;
    }
  }

  &__top {
    display: grid;
    grid-template-columns: 21px 1fr;
    gap: 91px;
    align-items: center;
    justify-items: left;
    height: 61px;
    margin-bottom: 250px;

    @include onTablet {
      grid-template-columns: 21px 1fr;
      gap: 59%;
      height: 72px;
      margin-bottom: 40px;
    }

    @include onDesktop {
      gap: 45%;
      margin-bottom: 119px;
    }
  }

  &__icon-menu {
    width: 18px;
    margin-left: 3px;
  }

  &__title {
    margin: 0 auto 22px;
    width: 280px;
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    font-size: 45px;
    line-height: 100%;
    text-align: center;
    color: $c-black;

    @include onTablet {
      margin: 0 0 20px;
      text-align: left;
    }

    @include onTablet768 {
      margin: 0 0 38px;
      width: 340px;
      font-size: 54px;
      line-height: 120%;
      text-align: left;
    }

    @include onDesktop {
      font-size: 72px;
      line-height: 120%;
      margin-bottom: 89px;
    }
  }

  &__button {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    width: 280px;
    height: 50px;
    align-items: center;
    text-decoration: none;
    font-family: "IBM Plex Sans", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    color: $c-black;
    border: 1px solid $c-green;
    box-sizing: border-box;
    transition: all $transitionTime;

    @include onTablet {
      margin: 0 0 20px;
      height: 70px;
      width: 270px;
    }

    @include onDesktop {
      margin-left: 200px;
    }

    &:active {
      background-color: $c-green;
      color: $c-white;
    }

    &--name {
      margin: 0 auto;
    }
  
    &--icon {
      height: 50px;
      width: 50px;
      background-image: url(../images/header/arrow.svg);
      background-color: $c-green;
      background-size: 8px;
      background-position: center;
      background-repeat: no-repeat;
      text-decoration: none;
      transition: all $transitionTime;

      @include onTablet {
        height: 70px;
        width: 70px;
      }
    }
  
    &:hover &--icon {
      width: 70px;

      @include onTablet {
        width: 100px;
      }
    }
  }

  &__events-button {
    display: none;
    height: 26px;
    width: 140px;
    text-decoration: none;
    align-items: center;
    rotate: -90deg;
    transition: all $transitionTime;

    @include onDesktop {
      display: flex;
    }

    &--line {
      width: 70px;
      height: 1px;
      background: $c-green;
      transition: all 0.1s;
    }

    &--name {
      padding: 0 7px 0 20px;
      font-family: "IBM Plex Sans", sans-serif;
      font-weight: 700;
      font-size: 14px;
      line-height: 100%;
      color: $c-darkGrey;
      transition: all 0.1s;
    }

    &:hover &--line {
      height: 26px;
    }

    &:hover &--name {
      color: $c-black;
    }

    &:active {
      background-color: $c-green;
    }

    &:active &--name {
      color: $c-white;
    }
  }
}
